import uniSwipeAction from '@/components/uni-ui/uni-swipe-action/uni-swipe-action.vue'
import uniSwipeActionItem from '@/components/uni-ui/uni-swipe-action-item/uni-swipe-action-item.vue'
import {mapState,mapGetters,mapActions,mapMutations} from 'vuex'
export default {
    components: {
        uniSwipeAction,
        uniSwipeActionItem
    },
	data() {
		return {
			index:-1,
			isedit:false,
			form:{
				name:"summer",
				phone:"32432453",
				path:"四川省 成都市 郫县",
				detailPath:"530街道",
				isDefault:true
			}
			
		}
	},
	onLoad({data}){
		if(data){
			let result = JSON.parse(data)
			this.form = result.item
			this.index = result.index
			this.isedit = true//修改状态为编辑
			uni.setNavigationBarTitle({
				'title':'修改收货地址'
			})
		}
		
	},
	methods: {
		...mapActions(['updatePathAction','createPathAction']),
		submit(){
			//现在的状态为编辑
			if(this.isedit){
				this.updatePathAction({
					index:this.index,
					item:this.form
				})
				// uni.showToast({
				// 	title: '修改成功'
				// });
				return uni.navigateBack({
					delta: 1
				});
				// console.log('test12')
			}
			//验证表单
			// console.log('test')
			this.createPathAction(this.form)
			uni.showToast({
				title: '创建成功'
			});
			// console.log(this.$store.state.path.list)
			uni.navigateBack({
				delta:1
			})
			// uni.navigateTo({
			// 	url:'/pages/user-path-list/user-path-list'
			// })
		}
	}
}
